.work-item-header {
  @extend %firstSectionPadding;
  padding-bottom: 220px;
  @include lg {
    padding-bottom: 108px; }
  @include sm {
    padding-bottom: 50px; }
  &__text {
    max-width: 510px; } }

.work-item-hero {
  position: relative;
  padding-bottom: 71.112%;
  display: flex;
  align-items: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 0;
  color: $white;
  // +rmin(1440)
  //   padding-bottom: 0
  //   height: 1024px
  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    img {
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      object-position: center; } }
  &__inner {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
  &__title {
    color: $white; }
  &__text {
    max-width: 510px; } }

.work-item-descr {
  &__top {
    margin-bottom: 203px;
    @include lg {
      margin-bottom: 100px; }
    @include sm {
      margin-bottom: 46px; } }
  &__col {
    &:not(:last-child) {
      @include sm {
        margin-bottom: 46px; } }
    p {
      max-width: 450px; } } }

.images-section {
  background-color: $imagesSectionBg;
  &_light {
    background-color: rgba(152, 149, 162, 0.1); }
  &__image {
    padding-top: 140px;
    @include lg {
      padding-top: 120px; }
    @include sm {
      padding-top: 30px; }
    &:last-child {
      padding-bottom: 140px;
      @include lg {
        padding-bottom: 120px; }
      @include sm {
        padding-bottom: 30px; } }
    img {
      max-width: 100%;
      height: auto;
      display: block; } } }

.text-section {
  padding: 202px 0 105px;
  @include lg {
    padding: 110px 0 49px; }
  @include sm {
    padding: 50px 0 22px;
    text-align: left; }
  // &__title
  //   margin-bottom: 10px
  //   +lg
  //     margin-bottom: 6px
  //   +sm
 }  //     margin-bottom: 0

.blockquote-section {
  padding: 205px 0;
  @include lg {
    padding: 112px 0; }
  @include sm {
    padding: 52px 0; }
  &__inner {
    max-width: 920px;
    width: 100%;
    margin: 0 auto;
    padding: 0 30px; } }
